//初始化项目以及版本信息

let versionValidateRules={
  VVersion: [
    { type: 'string', required: true, min: 1, max: 20, message: '标题长度必须在1-20个字符之间！', trigger: 'blur' }
  ],
  VStartDate: [
    { type: 'date', required: true, message: '请输入里程碑开始日期' }
  ],
  VReleaseDate: [
    { type: 'date', required: true, message: '请输入里程碑结束日期' }
  ]
}

export {
  versionValidateRules
}



