<template>
  <div :style="'overflow-y:auto;height:'+ containerHeight+'px'" >
    <project-info v-if="pageView === 'info'" :project="projectInfo"></project-info>
    <project-member v-if="pageView === 'member'" :projectID="projectID" :memberList="projectInfo.Members"></project-member>
    <project-mile-stone v-if="pageView === 'version'" :projectID="projectID" :versionList="projectInfo.Versions"></project-mile-stone>
    <project-module v-if="pageView === 'module'" :projectID="projectID" :moduleList="projectInfo.Modules"></project-module>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import labelEditorInput from '../../../components/common/LabelEditor-Input.vue'
import ProjectInfo from './ProjectInfo.vue'
import ProjectMember from './ProjectMember.vue'
import ProjectMileStone from './ProjectMileStone.vue'
import ProjectModule from './ProjectModule.vue'

export default {
  name: 'ProjectSettings',
  props: ['projectID','page'],
  data () {
    return {
      projectInfo: {}
    }
  },
  computed: {
    ...mapGetters(['appBodyHeight','userInfo']),
    ...mapGetters('projectglobal',['createDocumentType']),
    containerHeight: function () {
      return this.appBodyHeight
    },

    pageView: function () {
      return this.page.trim().toLowerCase()
    }
  },

  methods: {
    ...mapMutations('projectglobal',['setCreateDocumentType','setCreateDialogShow', 'setHeadMenu']),
    loadProject: function () {
      this.$axios.get('/api/project/' + this.projectID + '/detail?extinfo=1').then(response => {
        this.projectInfo = response.data.result
        console.log(this.projectInfo)
      }, response => {

      })
    }

  },

  created: function() {
    this.loadProject()
  },

  mounted: function () {
  },

  watch: {

    projectID: function (value) {
      this.loadProject()
    }

  },

  components: {
    labelEditorInput,
    ProjectInfo,
    ProjectMember,
    ProjectModule,
    ProjectMileStone
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">

.document-card {
  width: 200px;
  height: 150px;
  margin:10px;
  float:left;
}

</style>



